import { connect } from 'react-redux'
import { get } from 'lodash'
import { reduxForm } from 'redux-form'
import NumbersForm from './NumbersForm'
import { push } from 'react-router-redux'
import { create1300Service, getAvailable1300Numbers } from 'features/Numbers1300/Numbers1300.actions'
import { create1800Service, getAvailable1800Numbers } from 'features/Numbers1800/Numbers1800.actions'

const stateToProps = ({ form, numbers1300, numbers1800 }, { numberType }) => {
  const formName = numberType === '1300' ? 'numbers1300' : 'numbers1800'
  return {
    form: formName,
    formData: get(form, `${formName}.values`, {}),
    availableNumbers: numberType === '1300' 
      ? numbers1300.availableNumbers
      : numbers1800.availableNumbers,
  }
}

const dispatchToProps = {
  create1300Service,
  create1800Service,
  getAvailable1300Numbers,
  getAvailable1800Numbers,
  push,
}

export default connect(stateToProps, dispatchToProps)(reduxForm({
  destroyOnUnmount: false,
  forceUnregisterOnUnmount: true,
  initialValues: {
    step: 0,
    lastEdited: '',
    isPremiumNumber: false,
    isNewNumber: true,
    isExistingNumberInActive: false,
    extra: {
      directDebit: {
        method: 'CC',
      },
    },
    proofOfPurchase: {
      id: undefined,
      name: undefined,
    },
  },
})(NumbersForm))



