import i from 'icepick'

const initialState = {
  availableNumbers: [],
  availableNumbersGetLoading: [],
  services: [],
  servicesCreateLoading: false,
  servicesGetLoading: false,
  servicesUpdateLoading: false,
}

export default (state = initialState, action) => {
  switch (action.type) {
    case 'NUMBERS_1800/GET_AVAILABLE_NUMBERS_PENDING':
      return i.assoc(state, 'availableNumbersGetLoading', true)
    case 'NUMBERS_1800/GET_AVAILABLE_NUMBERS_REJECTED':
      return i.assoc(state, 'availableNumbersGetLoading', false)
    case 'NUMBERS_1800/GET_AVAILABLE_NUMBERS_FULFILLED':
      return i.chain(state)
        .assoc('availableNumbers', action.payload.data)
        .assoc('availableNumbersGetLoading', false)
        .value()

    case 'NUMBERS_1800/GET_SERVICES_PENDING':
      return i.assoc(state, 'servicesGetLoading', true)
    case 'NUMBERS_1800/GET_SERVICES_REJECTED':
      return i.assoc(state, 'servicesGetLoading', false)
    case 'NUMBERS_1800/GET_SERVICES_FULFILLED':
      return i.chain(state)
        .assoc('services', action.payload.data)
        .assoc('servicesGetLoading', false)
        .value()

    case 'NUMBERS_1800/CREATE_SERVICE_PENDING':
      return i.assoc(state, 'servicesCreateLoading', true)
    case 'NUMBERS_1800/CREATE_SERVICE_REJECTED':
      return i.assoc(state, 'servicesCreateLoading', false)
    case 'NUMBERS_1800/CREATE_SERVICE_FULFILLED':
      return i.chain(state)
        .updateIn(['services'], services => i.push(services, action.payload.data))
        .assoc('servicesCreateLoading', false)
        .value()

    case 'NUMBERS_1800/UPDATE_SERVICE_PENDING':
      return i.assoc(state, 'servicesUpdateLoading', true)
    case 'NUMBERS_1800/UPDATE_SERVICE_REJECTED':
      return i.assoc(state, 'servicesUpdateLoading', false)
    case 'NUMBERS_1800/UPDATE_SERVICE_FULFILLED': {
      const itemIndex = state.services.findIndex(service => service.id === action.payload.data.id)
      return i.chain(state)
        .assocIn(['services', itemIndex], action.payload.data)
        .assoc('servicesUpdateLoading', false)
        .value()
    }
  
    default:
      return state
  }
}



