import React, { Component } from 'react'

import { Container } from 'components/Layout'
import NumbersForm from 'features/Numbers/NumbersForm'
import HeadingBanner from 'components/Text/HeadingBanner'

import sText from 'components/Text/Text.scss'

export default class NumbersFormModal extends Component {
  static defaultProps = {
    numberType: '1300',
  }
  render() {
    const { productName, numberType, productId, modalCancel } = this.props
    return (
      <div>
        <HeadingBanner>
          <h2 className={ sText.heading3 }>Signup for a { numberType } Number</h2>
          <div className={ sText.descrip1 }>This won't take much more than 5 minutes.</div>
        </HeadingBanner>
        <Container
          paddingTop
          paddingBottom
          className="sm"
        >
          <NumbersForm
            numberType={ numberType }
            productId={ productId }
            productName={ productName }
            onCancel={ modalCancel }
          />
        </Container>
      </div>
    )
  }
}




