import React, { Component } from 'react'
import s from './LoadingLinear.scss'
import cn from 'classnames'

export default class extends Component {
  render() {
    const { type, className, style } = this.props
    const width = width >= 0 ? { width: `${width}%` } : {}
    return (
      <div className={ cn(s.progress, className) } style={ style }>
        <div className={ type == 'determinate' ? s.determinate : s.indeterminate } style={ width } />
      </div>
    )
  }
}



