import http from 'axios'
import config from 'config'

const baseUrl = 'https://cdn.contentful.com'

export const getEntries = ({ contentType }) => (dispatch, getState) => {
  if (!getState().cms[contentType]) {
    return dispatch({
      type: 'CMS/GET_ENTRIES',
      payload: http({
        method: 'GET',
        url: `${baseUrl}/spaces/${config.other.contentfulSpaceId}/entries`,
        headers: {
          Authorization: null,
        },
        params: {
          access_token: config.other.contentfulToken,
          content_type: contentType,
        },
      }),
      meta: {
        contentType,
      },
    })
  } 
  return new Promise(resolve => resolve())
}



