import http from 'axios'

export const get1300Numbers = () => ({
  type: 'NUMBERS_1300/GET_SERVICES',
  payload: http({
    method: 'GET',
    url: 'api/v1/swt/numbers/1300',
  }),
})

export const getAvailable1300Numbers = () => ({
  type: 'NUMBERS_1300/GET_AVAILABLE_NUMBERS',
  payload: http({
    method: 'GET',
    url: 'api/v1/swt/numbers/1300/available',
  }),
})

const getOverRides = (formData) => {
  // We do not destroy the data throughout the wizard so we need to manually set some values
  // to undefined as required. This depends on the form order.
  if (formData.isNewNumber) {
    return {
      existingNumberProvider: undefined,
      existingNumberAccount: undefined,
      erouHash: undefined,
    }
  } 
  if (formData.isExistingNumberInActive) {
    return {
      isSmartNumber: true,
      existingNumberProvider: undefined,
      existingNumberAccount: undefined,
    }
  } 
  return {
    isSmartNumber: undefined,
    erouHash: undefined,
  }
}

export const create1300Service = formData => ({
  type: 'NUMBERS_1300/CREATE_SERVICE',
  payload: http({
    method: 'POST',
    url: 'api/v1/swt/numbers/1300',
    data: {
      ban: '20002026',
      usagePlanAlias: 'swtInbound',
      extra: {
        customer: formData.extra.customer,
        complexRouting: false,
        usagePlan: formData.productId,
        directDebit: {
          method: formData.extra.directDebit.method,
          ...(
            formData.extra.directDebit.method === 'BA' 
              ? { bankAccount: formData.extra.directDebit.bankAccount } 
              : { creditCard: {
                name: formData.extra.directDebit.creditCard.name,
                number: formData.extra.directDebit.creditCard.number,
                cvc: formData.extra.directDebit.creditCard.cvc,
                expiry: `${formData.extra.directDebit.creditCard.expiryMonth}/${formData.extra.directDebit.creditCard.expiryYear.slice(2)}`,
              } }),
        },
      },
      number: formData.number,
      forwardingNumber: formData.forwardingNumber,
      isNewNumber: formData.isNewNumber,
      isSmartNumber: formData.isPremiumNumber,
      existingNumberProvider: formData.existingNumberProvider,
      existingNumberAccount: formData.existingNumberAccount,
      customerReference: formData.customerReference,
      erouHash: formData.proofOfPurchase.id,
      ...getOverRides(formData),
    },
  }),
})

export const update1300Service = order => ({
  type: 'NUMBERS_1300/UPDATE_SERVICE',
  payload: http({
    method: 'PUT',
    url: `api/v1/swt/numbers/1300/${order.id}`,
    data: order,
  }),
})



