import React, { Component } from 'react'

import Button from 'components/Button'
import SimpleTable from 'components/Tables/SimpleTable'

import cn from 'classnames'
import sLay from 'styles/modules/layout.scss'
import sText from 'components/Text/Text.scss'
import sMarg from 'styles/modules/margins.scss'

export default ({ prev, formData, product, submitting }) => (
  <div>
    <div className={ cn(sText.descrip3, sMarg.mb30) }>
        Here's the summary of your new service. If everything looks good, click 'finish' and we'll start the setup process. You'll be able to monitor the status from the your dashboard.
    </div>
    <SimpleTable>
      <div><div>Plan:</div><div>{product.name}</div></div>
      <div><div>Plan Cost:</div><div>${product.costPerMonth}/mth</div></div>
      <div><div>Number:</div><div>{formData.number} {formData.numberWithLetters && `(${formData.numberWithLetters})`}</div></div>
      <div><div>Forwarding Number:</div><div>{formData.forwardingNumber}</div></div>
      { formData.isNewNumber && formData.number && formData.isPremiumNumber && formData.premiumNumberMonthlyFee && (
        <div><div>Premium Number Fee:</div><div>${formData.premiumNumberMonthlyFee}/mth (12 month contract)</div></div>
      )}
    </SimpleTable>
    <div className={ cn(sText.descrip5, sMarg.mt30) }>
      <p>I wish to apply for the Inbound Service described in this application form and acknowledge that if my application is accepted it will be provided on the terms and conditions set out in this application form and Our Customer Terms. If there is an inconsistency between this application form and Our Customer Terms, this application form applies instead of Our Customer Terms to the extent of that inconsistency.</p>
      <p>I acknowledge that I have had the opportunity to review Our Customer Terms.</p>
      <p>You agree and will ensure that any of your related bodies corporate which receive services connected with this application form and your representatives are aware, that IP Hub may:</p>
      <p>(a) use and disclose information about you and each of them in accordance with IP Hubs Privacy Statement; and</p>
      <p>(b) give to, seek and obtain from, a credit reporting agency, information (including personal and credit information) about you to assess your creditworthiness.</p>
    </div>
    <div className={ cn(sLay['layout-column'], sLay['layout-align-center-center']) } style={ { marginTop: '40px' } }>
      <Button color="tertiary" type="submit" loading={ submitting }>
          Finish
      </Button>
      <br /><br />
      <Button plain onClick={ prev }>
          Back
      </Button>
    </div>
  </div>
)



