import initHttp from './initHttp'
import { getEntries } from 'features/Cms/Cms.actions'

export default (store) => {
  initHttp(store)
  return Promise.all([
    store.dispatch(getEntries({ contentType: 'product1300Plans' })),
    store.dispatch(getEntries({ contentType: 'product1800Plans' })),
  ])
}



