import React, { Component } from 'react'
import { FormSection } from 'redux-form'
import Button from 'components/Button'
import cn from 'classnames'
import sLay from 'styles/modules/layout.scss'
import sText from 'components/Text/Text.scss'
import sMarg from 'styles/modules/margins.scss'
import BillingAddCreditCardFormFields from 'features/Billing/BillingAddCreditCardForm/BillingAddCreditCardFormFields'
import BillingBankAccountFields from 'features/Billing/BillingBankAccountFields'
import { Row, Col } from 'components/Layout'

export default class NumbersBillingStep extends Component {
  render() {
    const { prev, change, formData } = this.props
    const method = formData.extra.directDebit.method
    return (
      <div>
        <div className={ cn(sText.descrip3, sMarg.mb30) }>
          Finally, we need your Billing details. You can pay with either credit card or bank deposit.
        </div>
        <Row className={ cn(sLay['layout-row'], sMarg.mb15) }>
          <Col className={ cn(sLay['flex-50'], sLay['layout-column']) } >
            <Button ghost style={ method === 'BA' ? { borderColor: 'transparent' } : {} } onClick={ () => change('extra.directDebit.method', 'CC') }>Credit Card</Button>
          </Col>
          <Col className={ cn(sLay['flex-50'], sLay['layout-column']) } >
            <Button ghost style={ method === 'CC' ? { borderColor: 'transparent' } : {} } onClick={ () => change('extra.directDebit.method', 'BA') }>Bank Deposit</Button>
          </Col>
        </Row>
        { method === 'CC' && 
          <FormSection name="extra.directDebit.creditCard">
            <BillingAddCreditCardFormFields />
          </FormSection> }
        { method === 'BA' && 
          <FormSection name="extra.directDebit.bankAccount">
            <BillingBankAccountFields />
          </FormSection> }
        <div className={ cn(sLay['layout-column'], sLay['layout-align-center-center']) } style={ { marginTop: '40px' } }>
          <Button color="tertiary" type="submit">
            Next
          </Button>
          <br /><br />
          <Button plain onClick={ prev }>
            Back
          </Button>
        </div>
      </div>
    )
  }
}




