import React, { Component } from 'react'

import { Form } from 'redux-form'
import NumbersProductSelectStep from 'features/Numbers/NumbersProductSelectStep'
import NumbersSelectNewNumberStep from 'features/Numbers/NumbersSelectNewNumberStep'
import NumbersTransferExistingStep from 'features/Numbers/NumbersTransferExistingStep'
import NumbersOtherSettingsStep from 'features/Numbers/NumbersOtherSettingsStep'
import NumbersSummaryStep from 'features/Numbers/NumbersSummaryStep'
import NumbersAccountStep from 'features/Numbers/NumbersAccountStep'
import NumbersBillingStep from 'features/Numbers/NumbersBillingStep'
import NumbersCompanyStep from 'features/Numbers/NumbersCompanyStep'
import { Dots, Dot } from 'components/Dots'
import sMarg from 'styles/modules/margins.scss'

export default class NumbersForm extends Component {
  componentWillMount() {
    const { change, productId, productName, formData, numberType, getAvailable1300Numbers, getAvailable1800Numbers } = this.props
    if (numberType === '1300') {
      getAvailable1300Numbers()
    } else {
      getAvailable1800Numbers()
    }
    if (formData.productId !== productId) {
      change('productId', productId)
      change('productName', productName)
      change('step', 0)
    }
    change('lastEdited', new Date())

    this.steps = [{
      wrapInForm: true,
      Component: props => <NumbersProductSelectStep prev={ props.onCancel } { ...props } />,
    }, {
      wrapInForm: true,
      Component: props => (!props.formData.isNewNumber
        ? <NumbersTransferExistingStep prev={ this.prevStep } { ...props } />
        : <NumbersSelectNewNumberStep prev={ this.prevStep } { ...props } />),
    }, {
      wrapInForm: true,
      Component: props => <NumbersOtherSettingsStep prev={ this.prevStep } { ...props } />,
    }, {
      wrapInForm: true,
      Component: props => <NumbersAccountStep prev={ this.prevStep } { ...props } />,
    }, {
      wrapInForm: true,
      Component: props => <NumbersCompanyStep prev={ this.prevStep } { ...props } />,
    }, {
      wrapInForm: true,
      Component: props => <NumbersBillingStep prev={ this.prevStep } { ...props } />,
    },  {
      wrapInForm: true,
      Component: props => <NumbersSummaryStep prev={ this.prevStep } { ...props } />,
    }]
  }
  submit = (formData) => {
    const { create1300Service, create1800Service, clearSubmitErrors, push, numberType, formData: { step }, reset } = this.props
    if (step === this.steps.length - 1) {
      if (numberType === '1300') {
        return create1300Service(formData).then(() => {
          reset()
          push('/order-complete')
        })
      } 
      return create1800Service(formData).then(() => {
        reset()
        push('/order-complete')
      })
    } 
    this.nextStep()
    clearSubmitErrors()
  }
  nextStep = () => {
    this.props.change('step', this.props.formData.step + 1)
  }
  prevStep = () => {
    this.props.change('step', this.props.formData.step - 1)
  }
  render() {
    const { handleSubmit, formData } = this.props    
    const step = this.steps[formData.step]
    if (step === undefined) {
      return null
    }
    return (
      <div>
        <Dots className={ sMarg.mb60to30 }>
          { this.steps.map((Step, idx) => <Dot key={ idx } active={ formData.step === idx } />) }
        </Dots>
        { step.wrapInForm
          ? (
            <Form onSubmit={ handleSubmit(this.submit) }>
              <step.Component { ...this.props } />
            </Form>
          )
          : <step.Component { ...this.props } />
        }
      </div>
    )
  }
}



