import React, { Component } from 'react'
import TransitionGroup from 'react-transition-group/TransitionGroup'
import CSSTransition from 'react-transition-group/CSSTransition'

import cn from 'classnames'
import s from './InputMessage.scss'

export default class InputMessage extends Component {
  static propTypes = {
  }
  render() {
    const { touched, error, warning } = this.props
    const isError = touched && error
    const show = warning || isError
    return (
      <TransitionGroup className={ s.container }>
        { show && 
          <CSSTransition
            key="message"
            timeout={ 200 }
            classNames={ {
              enter: s.fadeEnter,
              enterActive: s.fadeEnterActive,
              appear: s.fadeEnter,
              appearActive: s.fadeEnterActive,
              exit: s.fadeExit,
              exitActive: s.fadeExitActive,
            } }
          >
            <div className={ cn(s.message, { [s.error]: isError }) }>{ isError ? error : warning }</div>
          </CSSTransition>
        }
      </TransitionGroup>      
    )
  }
}




