import React, { Component } from 'react'

import { Field } from 'redux-form'
import { Input } from 'components/Input/Input'

import Button from 'components/Button'

import cn from 'classnames'
import sLay from 'styles/modules/layout.scss'
import sText from 'components/Text/Text.scss'
import sMarg from 'styles/modules/margins.scss'
import { vRequired, vEmail } from 'components/Input/Input.validations'
import { Row, Col } from 'components/Layout'

export default class NumbersAccountStep extends Component {
  render() {
    const { prev } = this.props
    return (
      <div>
        <div className={ cn(sText.descrip3, sMarg.mb30) }>
          Fill out some basic account details so we can keep you updated with your order status.
        </div>
        <Row className={ cn(sLay['layout-xs-col'], sLay['layout-gt-xs-row']) }>
          <Col className={ sLay['flex-gt-xs-50'] }>
            <Field
              label="First name"
              component={ Input }
              name="extra.customer.firstName"
              validate={ [vRequired] }
            />
          </Col>
          <Col className={ sLay['flex-gt-xs-50'] }>
            <Field
              label="Last name"
              component={ Input }
              name="extra.customer.surname"
              validate={ [vRequired] }
            />
          </Col>
        </Row>
        <Field
          label="Email"
          component={ Input }
          name="extra.customer.email"
          validate={ [vRequired, vEmail] }
        />
        <Field
          label="Contact phone number"
          component={ Input }
          name="extra.customer.phone"
          validate={ [vRequired] }
        />
        <div className={ cn(sLay['layout-column'], sLay['layout-align-center-center']) } style={ { marginTop: '40px' } }>
          <Button color="tertiary" type="submit">
            Next
          </Button>
          <br /><br />
          <Button plain onClick={ prev }>
            Back
          </Button>
        </div>
      </div>
    )
  }
}




