import { connect } from 'react-redux'
import NumbersProductSelectStep from './NumbersProductSelectStep'

const stateToProps = ({ cms }, { numberType }) => ({
  options: cms[numberType === '1300' ? 'product1300Plans' : 'product1800Plans'].map(item => ({
    name: `${item.fields.name} ($${item.fields.costPerMonth} per month)`,
    value: item.fields.id,
  })),
})

const dispatchToProps = {}

export default connect(stateToProps, dispatchToProps)(NumbersProductSelectStep)



