import analytics from './Analytics.utils'
import uuidv4 from 'uuid/v4'

const initialState = {
  anonymousId: uuidv4(),
}

export default (state = initialState, action) => {
  switch (action.type) {
    case '@@router/LOCATION_CHANGE': {
      if (!action.payload.hash) {
        setTimeout(analytics.page, 200)
      }
      return state
    }

    default:
      return state
  }
}



