import http from 'axios'
import Helmet from 'react-helmet'
import { store } from 'app/store'
import config from 'config'

import { throttle } from 'lodash'

const track = (event, properties) => dispatch => (false ? dispatch(http({
  method: 'POST',
  url: `${config.api.analytics}/api/v1/track`,
  data: {
    anonymousId: store.getState().analytics.anonymousId,
    event,
    properties,
  },
})) : '')

const identify = traits => dispatch => (false ? dispatch(http({
  method: 'POST',
  url: `${config.api.analytics}/api/v1/identify`,
  data: {
    anonymousId: store.getState().analytics.anonymousId,
    traits,
  },
})) : '')

const page = () => dispatch => (false ? dispatch(http({
  method: 'POST',
  url: `${config.api.analytics}/api/v1/page`,
  data: {
    anonymousId: store.getState().analytics.anonymousId,
    category: '',
    name: Helmet.peek().title,
    properties: {
      url: window.location.href,
      path: window.location.pathname,
      referrer: document.referrer,
    },
  },
})) : '')


const reset = () => {}

export default {
  track,
  identify,
  page: throttle(page, 200),
  reset,
}



