import 'babel-core/register'
import React from 'react'
import { render } from 'react-dom'
import Root from './Root'
import { configureStore } from './store'
import getInitialState from './state/getInitialState'
import persistConfig from './state/persistConfig'
import initApp from 'app/init'
import { createPersistor } from 'redux-persist'
import 'styles/modules/index.scss'
import './setGlobalEnv'
import showNumbersFormModal from 'features/Numbers/NumbersFormModal'

(async () => {
  // Add the iphub el
  const elementId = 'iphub-forms'
  const elem = document.createElement('div')
  elem.id = elementId
  document.body.appendChild(elem)
  const initialState = await getInitialState(persistConfig) || {}
  const store = configureStore(initialState)
  createPersistor(store, persistConfig)
  await initApp(store)
  render(<Root store={ store } />, document.getElementById(elementId))

  /** Create the window lib */
  window.iphubForms = {
    show: ({ numberType, productId }) => store.dispatch(showNumbersFormModal({ productId, numberType })),
  }
})()


