import React from 'react'
import cn from 'classnames'
import s from './LoadingDots.scss'
import sLay from 'styles/modules/layout.scss'
import IconSpinner from 'components/Icons/Spinner'

export default props => (
  <div className={ cn(sLay['layout-row'], sLay['layout-align-center'], props.className) } style={ props.style }>
    <IconSpinner className={ s.spinner } />
  </div>
)



