import i from 'icepick'
import { keyBy } from 'lodash'

const initialState = {
  casesById: {},
  cases: [],
  numberCases: {
    // number : []
  },
  getCasesLoading: false,
}


export default (state = initialState, action) => {
  switch (action.type) {
    case 'CASES/GET_CASES_PENDING':
      return i.assoc(state, 'getCasesLoading', true)
    case 'CASES/GET_CASES_REJECTED':
      return i.assoc(state, 'getCasesLoading', false)
    case 'CASES/GET_CASES_FULFILLED':
      return i.chain(state)
        .assoc('cases', action.payload.data.map(item => item.id))
        .updateIn(['casesById'], casesById => ({ ...casesById, ...keyBy(action.payload.data, 'id') }))
        .assoc('getCasesLoading', false)
        .value()

    case 'CASES/GET_NUMBER_CASES_PENDING':
      return i.assoc(state, 'getCasesLoading', true)
    case 'CASES/GET_NUMBER_CASES_REJECTED':
      return i.assoc(state, 'getCasesLoading', false)
    case 'CASES/GET_NUMBER_CASES_FULFILLED':
      return i.chain(state)
        .assocIn(['numberCases', action.meta.number], action.payload.data.map(item => item.id))
        .updateIn(['casesById'], casesById => ({ ...casesById, ...keyBy(action.payload.data, 'id') }))
        .assoc('getCasesLoading', false)
        .value()
    
    default:
      return state
  }
}



