import i from 'icepick'
import { pick } from 'lodash'
import analytics from 'features/Analytics/Analytics.utils'

const initialState = {
  emailUpdatePending: false,
  updatePasswordPending: false,
  showSecretStuff: false,

  savePending: false,

  token: null,
  expiration: null,
  user: {},
  loggedOut: true,

  loginForm: {
    step: 1,
  },
}


export default (state = initialState, action) => {
  switch (action.type) {
    case 'AUTH/TOGGLE_SECRET_STUFF':
      return i.assoc(state, 'showSecretStuff', action.payload)

    case 'AUTH/INIT_HTTP_HEADER':
      return state
    case 'AUTH/LOAD_USER_DATA_FULFILLED':
      return i.chain(state)
        .assoc('user', action.payload.data)
        .value()

    case 'AUTH/SET_AUTH_TOKEN':
      return { ...state,
        token: action.payload,
      }
    case 'AUTH/REMOVE_AUTH_TOKEN':
      return { ...state,
        token: null,
      }
    case 'AUTH/GET_TOKEN_FULFILLED':
      return { ...state,
        token: action.payload.data.token,
      }
    case 'AUTH/NEXT_LOGIN_STEP':
      return i.assocIn(state, ['loginForm', 'step'], state.loginForm.step + 1)
    case 'AUTH/PREV_LOGIN_STEP':
      return i.assocIn(state, ['loginForm', 'step'], state.loginForm.step - 1)
    case 'AUTH/LOGIN_FULFILLED': {
      const { user, expiredAt, token } = action.payload.data
      analytics.track('User - Login')
      return i.chain(state)
        .assoc('expiration', expiredAt)
        .assoc('token', token)
        .assoc('user', user)
        .assoc('loggedOut', false)
        .value()
    }
    case 'AUTH/REGISTER_FULFILLED': {
      const { user, expiredAt, token } = action.payload.data
      //      analytics.identify({
      //        firstName: user.firstName,
      //        lastName: user.lastName,
      //        email: user.email,
      //      })
      analytics.track('User - Register')
      return i.chain(state)
        .assoc('expiration', expiredAt)
        .assoc('token', token)
        .assoc('user', user)
        .assoc('loggedOut', false)
        .value()
    }
    case 'AUTH/UPDATE_USER':
      analytics.track('User - Update')
      return { ...state,
        user: action.payload.user,
      }
    case 'AUTH/LOGOUT':
      analytics.track('User - Logout - Manual')
      analytics.reset()
      return {
        ...state,
        ...initialState,
        loggedOut: true,
        showSecretStuff: state.showSecretStuff,
      }
    case 'AUTH/LOGOUT_TOKEN_EXPIRED':
      // analytics.track('User - Logout - Token Expired')
      // analytics.reset()
      return {
        ...state,
        ...initialState,
        loggedOut: true,
        showSecretStuff: state.showSecretStuff,
      }
    default:
      return state
  }
}



