import React, { Component } from 'react'

import { Field, FormSection } from 'redux-form'
import { Input } from 'components/Input/Input'

import Button from 'components/Button'

import cn from 'classnames'
import sLay from 'styles/modules/layout.scss'
import sText from 'components/Text/Text.scss'
import sMarg from 'styles/modules/margins.scss'
import { vRequired, vMinLength, vMaxLength } from 'components/Input/Input.validations'
import Address from 'features/Forms/Address'

const vAbnMinLength = vMinLength(11)
const vAbnMaxLength = vMaxLength(11)

export default class NumbersAccountStep extends Component {
  render() {
    const { prev, change } = this.props
    return (
      <div>
        <div className={ cn(sText.descrip3, sMarg.mb30) }>
          We also need to know your company's info for billing purposes. You may need to use <a href="https://abr.business.gov.au/" target="_blank" rel="noopener noreferrer">abn lookup</a> to find your abn.
        </div>
        <Field
          label="Company Name"
          component={ Input }
          name="extra.customer.company"
          validate={ [vRequired] }
        />
        <Field
          label="ABN"
          component={ Input }
          name="extra.customer.abn"
          validate={ [vRequired, vAbnMinLength, vAbnMaxLength] }
        />
        <FormSection name="extra.customer">
          <Address 
            change={ (field, val) => change(`extra.customer.${field}`, val) } 
            postCodeName="postcode"
          />
        </FormSection>
        <div className={ cn(sLay['layout-column'], sLay['layout-align-center-center']) } style={ { marginTop: '40px' } }>
          <Button color="tertiary" type="submit">
            Next
          </Button>
          <br /><br />
          <Button plain onClick={ prev }>
            Back
          </Button>
        </div>
      </div>
    )
  }
}




