import i from 'icepick'
import { keyBy } from 'lodash'

const initialState = {
  numbersById: {},
  numbers: [],
  numbersLoading: false,
  disconnectFees: {
    /**
     * [serviceId]: {
     *   fee: number,
     *   loading: boolean,
     *   submitting: boolean,
     * }
     */
  },
  callUsage: {
    /**
     * [serviceId-page]: [
     *   {data: callUsage, loading, numPages},
     *   {data: callUsage, loading, numPages},
     *   {data: callUsage, loading, numPages}
     * ]
     */
  },
  availableNumbers: [],
  availableLoading: false,
}

export default (state = initialState, action) => {
  switch (action.type) {
    case 'NUMBERS/GET_DISCONNECT_FEE_PENDING':
      return i.assocIn(state, ['disconnectFees', action.meta.serviceId, 'loading'], true)
    case 'NUMBERS/GET_DISCONNECT_FEE_REJECTED':
      return i.assocIn(state, ['disconnectFees', action.meta.serviceId, 'loading'], false)
    case 'NUMBERS/GET_DISCONNECT_FEE_FULFILLED':
      return i.assocIn(state, ['disconnectFees', action.meta.serviceId], {
        submitting: false,
        loading: false,
        fee: action.payload.data.disconnectionFee,
      })

    case 'NUMBERS/DISCONNECT_SERVICE_PENDING':
      return i.assocIn(state, ['disconnectFees', action.meta.serviceId, 'submitting'], true)
    case 'NUMBERS/DISCONNECT_SERVICE_REJECTED':
      return i.assocIn(state, ['disconnectFees', action.meta.serviceId, 'submitting'], false)
    case 'NUMBERS/DISCONNECT_SERVICE_FULFILLED':
      return i.assocIn(state, ['disconnectFees', action.meta.serviceId, 'submitting'], false)

    case 'NUMBERS/GET_CALL_USAGE_PENDING':
      return i.assocIn(state, ['callUsage', action.meta.cacheKey, 'loading'], true)
    case 'NUMBERS/GET_CALL_USAGE_REJECTED':
      return i.assocIn(state, ['callUsage', action.meta.cacheKey, 'loading'], false)
    case 'NUMBERS/GET_CALL_USAGE_FULFILLED':
      return i.chain(state)
        .assocIn(['callUsage', action.meta.cacheKey, 'data'], action.payload.data)
        .assocIn(['callUsage', action.meta.cacheKey, 'loading'], false)
        .assocIn(['callUsage', action.meta.cacheKey, 'numPages'], parseInt(action.payload.headers['x-total-count'] / 20, 10) + 1)
        .value()
  
    case 'NUMBERS/GET_NUMBERS_PENDING':
      return i.assocIn(state, ['numbersLoading'], true)
    case 'NUMBERS/GET_NUMBERS_REJECTED':
      return i.assocIn(state, ['numbersLoading'], false)
    case 'NUMBERS/GET_NUMBERS_FULFILLED':
      return i.chain(state)
        .assocIn(['numbersLoading'], false)
        .assocIn(['numbers'], action.payload.data.map(number => number.sid))
        .assocIn(['numbersById'], keyBy(action.payload.data, 'sid'))
        .value()
  
    case 'NUMBERS/GET_AVAILABLE_NUMBERS_PENDING':
      return i.assocIn(state, ['availableLoading'], true)
    case 'NUMBERS/GET_AVAILABLE_NUMBERS_REJECTED':
      return i.assocIn(state, ['availableLoading'], false)
    case 'NUMBERS/GET_AVAILABLE_NUMBERS_FULFILLED':
      return i.chain(state)
        .assocIn(['availableLoading'], false)
        .assocIn(['availableNumbers'], action.payload.data)
        .value()
  
    default:
      return state
  }
}



