import React, { Component, PropTypes } from 'react'

import { Field } from 'redux-form'
import { Input } from 'components/Input/Input'
import CheckboxRow from 'components/Input/CheckboxRow'
import { vRequired, vCreditCardNumber, vCreditCardCvc, vCreditCardMonth, vCreditCardYear } from 'components/Input/Input.validations'

import cn from 'classnames'
import sLay from 'styles/modules/layout.scss'
import sMarg from 'styles/modules/margins.scss'
import sText from 'components/Text/Text.scss'

export default class BillingAccountAddCreditCardFormFields extends Component {
  render() {
    const { hideTerms } = this.props
    return (
      <div>
        <Field
          name="name"
          label="Name on card"
          component={ Input }
          validate={ [vRequired] }
        />
        <div className={ sLay['layout-row'] }>
          <div className={ sLay.flex }>
            <Field
              name="number"
              label="Credit Card Number"
              component={ Input }
              validate={ [vRequired, vCreditCardNumber] }
              type="number"
            />
          </div>
          <div style={ { paddingLeft: '10px', width: '125px' } }>
            <Field
              name="cvc"
              label="CVC"
              component={ Input }
              validate={ [vRequired, vCreditCardCvc] }
            />
          </div>
        </div>
        <div className={ cn(sLay['layout-gt-xs-row'], sLay['layout-xs-column']) }>
          <div className={ cn(sLay['flex-gt-xs']) }>
            <Field
              name="expiryMonth"
              label="Expiry Month (MM)"
              type="number"
              component={ Input }
              validate={ [vRequired, vCreditCardMonth] }
            />
          </div>
          <div className={ cn(sLay['flex-gt-xs'], sMarg.pl10to0) }>
            <Field
              name="expiryYear"
              label="Expiry Year (YYYY)"
              type="number"
              component={ Input }
              validate={ [vRequired, vCreditCardYear] }
            />
          </div>
        </div>
        { !hideTerms && (
          <Field
            name="confirm"
            component={ CheckboxRow }
            style={ { margin: '30px 0' } }
            validate={ [vRequired] }
          >
            I understand and agree to the payment and direct debit <a href="/payment-terms" target="_blank" rel="noopener noreferrer">terms and conditions</a> and I acknowledge that these terms are binding on me or the company that I am authorised to represent.
          </Field>
        )}
      </div>
    )
  }
}



