import { connect } from 'react-redux'
import NumbersSummaryStep from './NumbersSummaryStep'
import { get } from 'lodash'

const stateToProps = ({ cms }, { formData, numberType }) => ({
  product: get(cms[numberType === '1300' ? 'product1300Plans' : 'product1800Plans'].filter(item => item.fields.id === formData.productId), '0.fields', {}),
})

const dispatchToProps = {}

export default connect(stateToProps, dispatchToProps)(NumbersSummaryStep)



