import React, { Component } from 'react'
import { Field } from 'redux-form'
import Radio from 'components/Input/Radio'
import SelectPretty from 'components/Input/SelectPretty'
import Button from 'components/Button'

import cn from 'classnames'
import sLay from 'styles/modules/layout.scss'
import sText from 'components/Text/Text.scss'
import sMarg from 'styles/modules/margins.scss'
import { vRequired } from 'components/Input/Input.validations'

export default class NumbersProductSelectStep extends Component {
  render() {
    const { prev, options, numberType } = this.props
    
    return (
      <div>
        <div className={ cn(sText.descrip3, sMarg.mb30) }>
          What sort of service are you signing up for? Do you already have an existing {numberType} number or do you want a new one?
        </div>
        <Field
          label="Selected Service"
          component={ SelectPretty }
          options={ options }
          name="productId"
          validate={ [vRequired] }
        />
        <Field
          label="I want a new number."
          component={ Radio }
          name="isNewNumber"
          radioValue
          style={ { marginBottom: '15px' } }
        />
        <Field
          label="I already have a number."
          component={ Radio }
          name="isNewNumber"
          radioValue={ false }
        />
        <div className={ cn(sLay['layout-column'], sLay['layout-align-center-center']) } style={ { marginTop: '40px' } }>
          <Button color="tertiary" type="submit">
            Next
          </Button>
          <br /><br />
          <Button plain onClick={ prev }>
            Back
          </Button>
        </div>
      </div>
    )  
  }
}



