import http from 'axios'

export const uploadFile = ({ id, file }) => {
  const formData = new FormData()
  formData.append('file', file)

  return {
    type: 'FILES/UPLOAD',
    payload: http({
      method: 'POST',
      url: 'api/v1/numbers/erou',
      data: formData,
    }),
    meta: {
      id,
    },
  }
}



