import { connect } from 'react-redux'
import FileInput from './FileInput'
import { uploadFile } from '../Files.actions'

const mapStateToProps = ({ files }, { input }) => {
  const file = files[input.name] || {}
  return {
    loading: file.loading || false,
  }
}

const dispatchToProps = {
  uploadFile,
}

export default connect(mapStateToProps, dispatchToProps)(FileInput)



